import { createSlice } from "@reduxjs/toolkit";

// createing reducer with intial value
export const clientReducer = createSlice({
  name: "client_store",
  initialState: {
    isAuthenticated: false,
    queue: {},
    dashboard: {
      nav_trace: "dashboard",
    },
  },
  reducers: {
    startclientAction: (state, action) => {
      return {
        ...state,
        queue: action.payload,
        isAuthenticated: true,
      };
    },
    dispathcDashboardNavigator: (state, action) => {
      const { item } = action.payload;
      return {
        ...state,
        dashboard: {
          nav_trace: item,
        },
      };
    },
    logOut: (state, action) => {
      return {
        ...state,
        queue: [],
        isAuthenticated: false,
      };
    },
  },
});

export const { startclientAction, logOut, dispathcDashboardNavigator } =
  clientReducer.actions;

export default clientReducer.reducer;
